
/* Default style starts*/
.card.default{
    @apply flex flex-col max-w-[320px] 
    rounded-xl shadow-md pt-[50px] pb-5;
    background: linear-gradient(
        to bottom,
        #585dc6 110px,
        #fff6f6 110px
    )
}

.card.default img{
    /* border: 1px solid black; */
    @apply h-[100px] w-[100px] mx-auto rounded-[50%] shadow-sm border-[5px] border-white;
}

.card.default .details{
    @apply  mb-6 px-6 text-center;   
}

.card.default .details .title{
    @apply text-18 mt-1 font-bold leading-none;
    color: #585dc6;
}

.card.default .details h5{
    @apply text-14 mt-2 font-[400] leading-none;
}

.card.default .details .description{
    @apply w-full mt-3 text-16 text-left font-[400];
}

.card.default .buttons{
    @apply mt-auto  flex justify-around text-center;
}

.card.default .buttons>*:nth-child(odd){
    border-color: #585dc6;
    background-color: #585dc6;
    color: #fff6f6;
}

.card.default .buttons>*:nth-child(even){
    border-color: #585dc6;
    background-color: #fff6f6;
    color: #585dc6;
}

.card.default .buttons>*{
    @apply inline-block min-w-[100px] py-2 rounded-[24px]
    border-solid border-2
    shadow-sm;
}

/* Default style ends*/


/* Boat-lifeStyle Jemimah Rodrigues profile card starts */
.card.boat-lifeStyle{
    @apply w-[320px]
}
.card.boat-lifeStyle .profileImage{
    @apply w-full h-auto;
}
.card.boat-lifeStyle .bottomSection{
    @apply bg-[#eff4f7] pt-[1.5rem] pb-[1rem] px-2 text-center;
}
.card.boat-lifeStyle .details .title{
    @apply text-28 font-bold leading-none mb-2;
}
.card.boat-lifeStyle .bottomSection .buttons{
    @apply mt-2 flex flex-col gap-y-2;
}
.card.boat-lifeStyle .bottomSection .buttons>*{
    @apply text-[#2f5b96] block text-14 leading-[1.5] font-bold 
    after:bg-[url('../../../assets/icons/circle-with-arrow.svg')] after:w-[14px] after:inline-block after:h-[14px] after:align-baseline after:ml-1;
}
/* Boat-lifeStyle Jemimah Rodrigues profile card ends */


/* boat-lifeStyle-blog-card profile card starts */
.card.boat-lifeStyle-blog-card{
    @apply max-w-[320px] bg-[#fafafa] rounded-xl border-[1px] border-solid border-[#ecf0f4] overflow-hidden; 
}

.card.boat-lifeStyle-blog-card .details{
    @apply px-3 pt-3;
}

.card.boat-lifeStyle-blog-card .details .date{
    @apply text-12 leading-[1.167] mb-[7.5px] font-semibold text-[#868c91];
}

.card.boat-lifeStyle-blog-card .details .title{
    @apply text-16 md:text-18 font-bold leading-[1.2] text-[#191e23] mb-[7.5px] text-clamp text-clamp-2;
}

.card.boat-lifeStyle-blog-card .details .description{
    @apply text-12 leading-[1.167] mb-[15px] text-[#4e5358] text-clamp text-clamp-2;
}

.card.boat-lifeStyle-blog-card .buttons{
    @apply mx-3 mb-6;
}
.card.boat-lifeStyle-blog-card .buttons .button{
    @apply block w-full text-12 text-center text-[#1a2024] font-bold bg-white 
    border-[1px] border-solid border-[#d0d9de] rounded-[20px] leading-[38px]
     after:text-[#1a2024]  after:w-[15px] after:h-[15px] after:bg-contain after:inline-block  after:align-middle after:ml-1;
}

.card.boat-lifeStyle-blog-card .buttons .button::after{
    background-color: #1a2024;
    -webkit-mask-image: url('../../../assets/icons/circle-with-arrow.svg');
    mask-image: url('../../../assets/icons/circle-with-arrow.svg');
    mask-size: cover;
}


/* boat-lifeStyle-blog-card profile card ends */